<template>
  <section class="video-section">
    <div class="video">
      <div class="video__container">
        <div class="responsive-wrapper responsive-wrapper--16_9">
          <iframe allowFullScreen frameBorder="0" className="video" :src="videoUrl"></iframe>
        </div>
      </div>
      <template v-if="selectedVideo">
        <h1 class="video__title">{{selectedVideo.snippet.title}}</h1>
        <p class="video__description">{{selectedVideo.snippet.description}}</p>
      </template>
    </div>
    <ul class="video-list">
      <li class="video-item" :key="v.id.videoId" v-for="v in videos" @click="selectedVideo = v">
        <img class="video-item__thumb" :src="v.snippet.thumbnails.medium.url" alt="">
        <div class="video-item__info">
          <h4 class="video-item__title">{{v.snippet.title}}</h4>
        </div>
      </li>
    </ul>
  </section>
</template>

<script>
import {fetchVideos} from '@/api';

export default {
  data() {
    return {
      videoPayload: {},
      selectedVideo: null
    };
  },

  computed: {
    videos() {
      return this.videoPayload.items;
    },
    videoUrl() {
      if (!this.selectedVideo) {
        return '';
      }
      return `https://www.youtube.com/embed/${this.selectedVideo.id.videoId}`;
    }
  },

  async mounted() {
    this.videoPayload = await fetchVideos();
    await this.$nextTick();
    this.$el.querySelector('.video-list li:first-child').click();
  }
};
</script>

<style lang="scss" scoped>
.video-section {
  display: flex;
}

.vidoe {

}

.video__container {
  background-color: rgba(0,0,0,0.2);
  // max-width: 50vw;
}

.video__title {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.video__description {
  border-top: 2px solid rgba(0,0,0,0.2);
  font-size: 1.3rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.video-list {
  margin-left: auto;
  overflow-y: auto;
  overflow-x: hidden;
  width: 420px;
  flex-shrink: 0;
  max-height: 70vh;
}

.video-item {
  cursor: pointer;
  transition: 0.2s;
  display: flex;
}

.video-item:hover {
  background-color: rgba(0,0,0,0.1);
}

.video-item + .video-item {
  margin-top: 1rem;
}

.video-item__thumb {
  max-width: 200px;
}

.video-item__info {
  padding-left: 1rem;
}

.video-item__title {
  margin-top: 0;
  // overflow: hidden;
  // text-overflow: ellipsis;
  // white-space: nowrap;
}

@media screen and (max-width: 768px) {
  .video-section {
    flex-direction: column;
  }

  .video-list  {
    border-top: 2px solid rgba(0,0,0,0.8);
    width: 100%;
    margin: 1rem 0 0 0;
    padding: 3rem 0 0 0;
  }
}
</style>
