<template>
  <section class="tidbits-section">
    <div class="swiper-container">
      <!-- Additional required wrapper -->
      <div class="swiper-wrapper">
        <!-- Slides -->
        <div :key="v.id.videoId" v-for="v in videos" class="swiper-slide">
          <img :src="v.snippet.thumbnails.medium.url" alt="">
        </div>
      </div>
      <!-- If we need pagination -->
      <div class="swiper-pagination"></div>

      <!-- If we need navigation buttons -->
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>

      <!-- If we need scrollbar -->
      <div class="swiper-scrollbar"></div>
    </div>
  </section>
</template>

<script>
import {fetchVideos} from '@/api';

export default {
  data() {
    return {
      videoPayload: {},
      selectedVideo: null
    };
  },

  computed: {
    videos() {
      return this.videoPayload.items;
    },
    videoUrl() {
      if (!this.selectedVideo) {
        return '';
      }
      return `https://www.youtube.com/embed/${this.selectedVideo.id.videoId}`;
    }
  },

  async mounted() {
    this.videoPayload = await fetchVideos();
    await this.$nextTick();
    new Swiper('.swiper-container', {
      // Optional parameters
      // direction: 'vertical',
      slidesPerView: 1,
      spaceBetween: 0,
      grabCursor: true,
      freeMode: true,
      mousewheel: true,

      // If we need pagination
      pagination: {
        el: '.swiper-pagination',
        clickable: true
      },

      // Navigation arrows
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev'
      },

      // And if we need scrollbar
      scrollbar: {
        el: '.swiper-scrollbar'
      },

      breakpoints: {
        640: {
          slidesPerView: 2,
          spaceBetween: 20
        },
        768: {
          slidesPerView: 3,
          spaceBetween: 40
        },
        1024: {
          slidesPerView: 4,
          spaceBetween: 50
        }
      }
    });
  }
};
</script>

<style lang="scss" scoped>

.tidbits-section {
}

.swiper-container {
  width: 100%;
  height: 300px;
}

.swiper-slide {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
